// const BASE_URL = 'http://opt.uvigo.es'
const BASE_URL = process.env.REACT_APP_IS_LOCAL === 'true' ? 'http://localhost:5000' : ''

/**
 * @param {string[][]} arr
 * @returns {object}
 */
const parse_json = (arr) => arr.map(el => el.map(data => JSON.parse(data)))

export const apiService = {
    getForecast: async () => parse_json(await fetch(`${BASE_URL}/forecast`).then(res => res.json())),
    getPrevforecast: async () => parse_json(await fetch(`${BASE_URL}/prevforecast`).then(res => res.json())),
    getReal: async () => parse_json(await fetch(`${BASE_URL}/real`).then(res => res.json())),
}