import React, { forwardRef, useMemo } from 'react'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  SubTitle,
} from 'chart.js'
import { Line } from 'react-chartjs-2'
import { DateTime } from 'luxon'

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  SubTitle,
  Tooltip,
  Legend,
)

/**
 * @param {{
 * forecast: number[]
 * prevforecast: number[]
 * real: number[]
 * title: string
 * xLabel: string
 * ratio: number
 * }} props
 */

const Chart = forwardRef(
  ({ forecast, title, error, prevforecast, real, xLabel, ratio }, ref) => {
    const labels = useMemo(
      () =>
        [
          ...Array(48)
            .fill(0)
            .map((_, i) => DateTime.now().minus({ hours: 24 - 0.5 * i })),
          ...Array(48)
            .fill(0)
            .map((_, i) => DateTime.now().plus({ hours: 24 + 0.5 * i }))
        ].map((el, i) => el.toFormat('h:mm a')),
      []
    )

    const options = useMemo(
      () => ({
        responsive: true,
        maintainAspectRatio: false,
        aspectRatio: ratio,
        plugins: {
          legend: {
            position: 'top'
          },
          title: {
            display: true,
            text: `${title} Forecasting`,
            color: '#424242',
            font: {
              size: 20
            }
          },
          subtitle: {
            display: true,
            text: `MAPE: ${error} %`,
            color: '#424242',
            font: {
                size: 16,
                style: 'italic'
            },
            padding: {
                bottom: 10
            }
          },
        },
        scales: {
            x: {
                title: {
                    display: true,
                    text: 'Time',
                    color: '#424242',
                },
                ticks: {
                    callback: value => {
                        return labels[value]
                    }
                }
            },
            y: {
                title: {
                    display: true,
                    text: xLabel,
                    color: '#424242',
                }
            }
        }
        
      }),
      [title, error, xLabel, labels, ratio]
    )

    const data = useMemo(
      () => ({
        labels,
        datasets: [
          {
            label: 'Actual Forecast',
            data: [...Array(48), ...forecast],
            borderColor: 'rgb(229, 115, 115)',
            backgroundColor: 'rgba(229, 115, 115, 0.5)'
          },
          {
            label: 'Previous Forecast',
            data: prevforecast,
            borderColor: 'rgb(66, 66, 66)',
            backgroundColor: 'rgba(66, 66, 66, 0.5)'
          },
          {
            label: 'Real values',
            data: real,
            borderColor: 'rgb(120, 144, 156)',
            backgroundColor: 'rgba(120, 144, 156, 0.5)'
          }
        ]
      }),
      [labels, forecast, prevforecast, real]
    )

    return (
      <div ref={ref}>
        <Line options={options} data={data} />
      </div>
    )
  }
)

export default Chart

