import './App.css'
import React, { useState, useEffect, useRef } from 'react'
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import Chart from './components/Chart'
import 'bootstrap/dist/css/bootstrap.min.css';
import { apiService } from './services'
import { Wrapper, Status } from "@googlemaps/react-wrapper";

const initData = {
  co2: [],
  temp: []
}
const initError = {
    co2: 0,
    temp: 0,
}

function App() {
  const [forecast, setForecast] = useState(initData)
  const [prevforecast, setPrevforecast] = useState(initData)
  const [real, setReal] = useState(initData)
  const [ratio, setRatio] = useState(undefined)
  const [error, setError] = useState(initError)
  const ref_map = React.useRef(null)
  const [map, setMap] = React.useState()
  const ref = useRef()
  const Map = () => {
    return(
        <div>
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d769.8942081662018!2d-0.3382936110670525!3d39.47888764084939!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0xd6048877a05989b%3A0x565bba9154152bac!2zMznCsDI4JzQyLjgiTiAwwrAyMCcxNS45Ilc!5e0!3m2!1ses!2scu!4v1671567168488!5m2!1ses!2scu" width="100%" height="100%" style={{border:0}} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
        </div>
    )
  }
  const chartRef1 = useRef(null)
  const chartRef2 = useRef(null)

  useEffect(() => {
    const fetchData = () => {
      apiService.getForecast().then(data => {
        setForecast({
          co2: data[0][0].co2[0],
          temp: data[0][1].temp[0]
        })
      })
    }
    fetchData()
    const interval = setInterval(fetchData, 1000 * 60 * 5)
    return () => {
      clearInterval(interval)
    }
  }, [setForecast])

  useEffect(() => {
    const fetchData = () => {
      apiService.getPrevforecast().then(data => {
        setPrevforecast({
          co2: data[0][0].co2[0],
          temp: data[0][1].temp[0]
        })
      })
    }
    fetchData()
    const interval = setInterval(fetchData, 1000 * 60 * 5)
    return () => {
      clearInterval(interval)
    }
  }, [setPrevforecast])

  useEffect(() => {
    const fetchData = () => {
      apiService.getReal().then(data => {
        setReal({
          co2: data[0][0].co2,
          temp: data[0][1].temp
        })
      })
    }
    fetchData()
    const interval = setInterval(fetchData, 1000 * 60 * 5)
    return () => {
      clearInterval(interval)
    }
  }, [setReal])

  useEffect(() => {
    let sum_co2 = 0;
    let sum_temp = 0;

    for (let i = 0; i < 48; i++) {
        sum_co2 += ((Math.abs(forecast.co2[i] - real.co2[i]))/real.co2[i]) * 100;
        sum_temp += ((Math.abs(forecast.temp[i] - real.temp[i]))/real.temp[i]) * 100;
    }

    const mapeCo2 = sum_co2 / 48;
    const mapeTemp = sum_temp / 48;

    setError({
        co2: mapeCo2.toFixed(4),
        temp: mapeTemp.toFixed(4)
    })

  }, [real, forecast]);

  useEffect(() => {
    const aux = () => {
      if (ref.current) {
        const isLandscape = window.innerWidth >= window.innerHeight

        let height = ref.current.clientHeight
        let width = ref.current.clientWidth

        if (isLandscape) {
          width /= 2
          width -= 30
          height -= 40
        } else {
          height /= 2
          height -= 30
          width -= 40
        }

        chartRef1.current.style.height = height + 'px'
        chartRef1.current.style.width = width + 'px'

        chartRef2.current.style.height = height + 'px'
        chartRef2.current.style.width = width + 'px'
        // setRatio(width / height)
      }
    }

    aux()

    window.addEventListener('resize', aux)

    return () => {
      window.removeEventListener('resize', aux)
    }
  }, [])

  console.log({ ratio })

  function MyModal() {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
        <Button className='modal-button' size='sm' variant='outline-light' onClick={handleShow}>
          i
        </Button>

        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
            <Modal.Title>Device Info</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p>Department: UPV-FISICA </p>
                <Map />
            </Modal.Body>
            <Modal.Footer>
                <p>
                    *MAPE: Mean Absolute Percentage Error
                </p>
            </Modal.Footer>
        </Modal>
        </>
    );
  }

  return (
    <>
      <header>
          <h1>Energesis Forecast</h1>
          <MyModal />
          
      </header>
      <main ref={ref}>
        <Chart
          ref={chartRef1}
          title="CO2"
          error={error.co2}
          forecast={forecast.co2}
          prevforecast={prevforecast.co2}
          real={real.co2}
          xLabel="CO2 (ppm)"
          ratio={ratio}
        />
        <Chart
          ref={chartRef2}
          title="Temperature"
          error={error.temp}
          forecast={forecast.temp}
          prevforecast={prevforecast.temp}
          real={real.temp}
          xLabel="TEMP (C)"
          ratio={ratio}
        />
      </main>
    </>
  )
}

export default App
